@use '../abstract/variables' as v;
@use '../abstract/mixins' as m;

.text-primary{
    color: v.$primary;
}

.text-secondary{
    color: v.$secondary;
}

.text-success{
    color: v.$success;
}

.text-white{
    color: v.$white;
}

.text-danger{
    color: v.$danger;
}

.text-warning{
    color: v.$warning;
}

.text-muted{
    color: v.$secondary-alt;
}

.fw-bold{
    font-weight: 700;
}

.fw-semibold{
    font-weight: 600;
}

.w-100{
    width: 100%;
}

