$padding : 5;

@for $i from 0 through $padding{
    .p-#{$i} {
        padding: (($i)+rem)!important;
    }
}

@for $i from 0 through $padding{
    .pt-#{$i} {
        padding-top: (($i)+rem)!important;
    }
}

@for $i from 0 through $padding{
    .pb-#{$i} {
        padding-bottom: (($i)+rem)!important;
    }
}
@for $i from 0 through $padding{
    .ps-#{$i} {
        padding-left: (($i)+rem)!important;
    }
}
@for $i from 0 through $padding{
    .pe-#{$i} {
        padding-right: (($i)+rem)!important;
    }
}

@for $i from 0 through $padding{
    .px-#{$i} {
        padding: 0 (($i)+rem)!important;
    }
}

@for $i from 0 through $padding{
    .py-#{$i} {
        padding: (($i)+rem) 0!important;
    }
}

// Break Points

@media (max-width: v.$sm-breakpoint) {

    @for $i from 0 through $padding{
        .p-xs-#{$i} {
            padding: (($i)+rem)!important;
        }
    }
    
    @for $i from 0 through $padding{
        .pt-xm-#{$i} {
            padding-top: (($i)+rem)!important;
        }
    }
    
    @for $i from 0 through $padding{
        .pb-xm-#{$i} {
            padding-bottom: (($i)+rem)!important;
        }
    }
    
    @for $i from 0 through $padding{
        .px-xm-#{$i} {
            padding: 0 (($i)+rem)!important;
        }
    }
    
    @for $i from 0 through $padding{
        .py-xm-#{$i} {
            padding: (($i)+rem) 0!important;
        }
    }
  }

  @media (min-width:v.$sm-breakpoint) and (max-width: v.$md-breakpoint) {

    @for $i from 0 through $padding{
        .p-sm-#{$i} {
            padding: (($i)+rem)!important;
        }
    }
    
    @for $i from 0 through $padding{
        .pt-sm-#{$i} {
            padding-top: (($i)+rem)!important;
        }
    }
    
    @for $i from 0 through $padding{
        .pb-sm-#{$i} {
            padding-bottom: (($i)+rem)!important;
        }
    }
    
    @for $i from 0 through $padding{
        .px-sm-#{$i} {
            padding: 0 (($i)+rem)!important;
        }
    }
    
    @for $i from 0 through $padding{
        .py-sm-#{$i} {
            padding: (($i)+rem) 0!important;
        }
    }
  }

  @media (min-width:v.$md-breakpoint) and (max-width: v.$lg-breakpoint) {

    @for $i from 0 through $padding{
        .p-md-#{$i} {
            padding: (($i)+rem)!important;
        }
    }
    
    @for $i from 0 through $padding{
        .pt-md-#{$i} {
            padding-top: (($i)+rem)!important;
        }
    }
    
    @for $i from 0 through $padding{
        .pb-md-#{$i} {
            padding-bottom: (($i)+rem)!important;
        }
    }
    
    @for $i from 0 through $padding{
        .px-md-#{$i} {
            padding: 0 (($i)+rem)!important;
        }
    }
    
    @for $i from 0 through $padding{
        .py-md-#{$i} {
            padding: (($i)+rem) 0!important;
